/* Core layout */
.container {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.calendar {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  max-width: 1200px;
  justify-content: center;
}

/* Individual door and layout */
.door-wrapper {
  width: 140px;
  height: 170px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.door-date {
  font-size: 0.95rem;
  margin-bottom: 6px;
  text-align: center;
  font-weight: 600;
}

.door {
  width: 100%;
  height: 140px;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  cursor: not-allowed;
}

.door.clickable {
  cursor: pointer;
}

.door-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.blurred {
  filter: blur(5px);
}

.door-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  text-align: center;
}

.door-front {
  background: rgba(0, 0, 0, 0.5);
  padding: 6px 8px;
  border-radius: 6px;
}

.countdown {
  font-size: 0.9rem;
}

.door-message {
  background: rgba(0, 0, 0, 0.65);
  padding: 10px;
  border-radius: 8px;
  font-size: 0.95rem;
  pointer-events: all;
  cursor: pointer;
}

/* Modal styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadeIn 0.2s ease;
  z-index: 9999;
}

.modal-content {
  background: white;
  color: black;
  padding: 20px 30px;
  border-radius: 12px;
  max-width: 90%;
  max-height: 80%;
  overflow-y: auto;
  box-shadow: 0 6px 20px rgba(0,0,0,0.4);
  position: relative;
  animation: zoomIn 0.2s ease;
  z-index: 10000;
}

.modal-close {
  position: absolute;
  top: 8px;
  right: 12px;
  background: none;
  border: none;
  font-size: 1.8rem;
  color: #555;
  cursor: pointer;
  z-index: 10001;
}

body {
  margin: 0;
  font-family: sans-serif;
  background-image: url("./calendar_backdrop.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes zoomIn {
  from { transform: scale(0.8); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}
